import _ from 'lodash';
import moment from 'moment';
import User from '../models/User';
import express from 'express';
const router = express.Router();

router.get('/fetch-user-setting', (req, res) => {

	let userList = []

	Promise.all(req.user.userList.map( async (user) => {
		await User.findOne({ username: user }, 'username createdAt accountActive maxParlayTeam weeklyStartBalance maxWinAmount maxWagerAmount minRiskAmount', (err, userInfo) => {
			userList.push(userInfo)
		})
	})).then(() => {
		res.json(userList)
	})

})

export default router;